<template>
  <section class="hero octogoin-color is-fullheight">
    <div class="hero-body">
      <div class="container">
        <div class="columns is-centered">
          <div class="column is-5">
            <p class="title is-1 has-text-white main-title">{{title}}</p>
            <p class="subtitle is-4 has-text-white">{{subtitle}}</p>
          </div>
          <div class="column is-2">
            <figure class="image is-128x128">
              <img src="/static/logo-no-text.png">
            </figure>
          </div>
        </div>
        <br>
        <div class="columns is-centered">
          <div class="column">
            <p class="subtitle is-4 has-text-white">Lets get started.</p>
          </div>
        </div>
        <div class="columns is-centered">
          <div class="column has-text-white">
              <span class="icon is-medium" v-scroll-to="{el: '#how-it-works'}">
              <i class="fa fa-arrow-circle-down"></i>
            </span>
          </div>
        </div>
        
      </div>
    </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      title: 'Welcome to Octogoin',
      subtitle: 'No Gambling. No Deposits. 100% Skins.',
    };
  },
};
</script>

<style scoped>
.main-title {
  padding-bottom: 20px;
}
</style>




