import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/Main';
import Profile from '@/components/Profile';
import Earn from '@/components/Earn';
import Faucet from '@/components/Faucet';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Main,
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
    },
    {
      path: '/earn',
      name: 'Earn',
      component: Earn,
    },
    {
      path: '/faucet',
      name: 'Faucet',
      component: Faucet,
    },
  ],
});



